import React from "react";
import MultiCartItemAttributeComponent from "./MultiCartItemAttributeComponent";
import MultiCartItemIconCellComponent from "./MultiCartItemIconCellComponent";
import OraStockLevelStatusComponent from "eshop/components/OraStockLevelStatusComponent";
import OnlineUtils from "eshop/utils/OnlineUtils";
import ProductOfferPriceTooltipComponent from "eshop/modules/simfree/components/ProductOfferPriceTooltipComponent";

const preventDefaultWrapper = (handler) => (event) => {
    event.preventDefault();
    handler(event);
};

export default class MultiCartItemHeaderComponent extends MultiCartItemAttributeComponent {
    constructor (props) {
        super(props);
    }

    createDescription() {
        switch (this.props.processType) {
            case 'INSTALMENT_SALES_OF_GOODS':
            case 'INSTALMENT_SALES_OF_GOODS_NC':
                return this.createInstallmentDescription.apply(this);
            default:
                return this.createDefaultDescription.apply(this);
        }
    }

    createInstallmentDescription() {
        return (
            <td colSpan={this.props.priceless && '3'} className="u-padding-right-l">
                <div className="u-padding-top-l u-padding-l">
                    <span className="u-font-bold">{this.props.title}</span>
                    <div className="u-spacing">{this.props.installmentDescription}</div>
                    <div>
                        <a href="#" onClick={preventDefaultWrapper(this.props.onRemoveClicked)} aria-label="usuń pozycję z koszyka">Usuń</a>
                    </div>
                </div>
            </td>
        );
    }

    showLowerInstallments(){
        return this.props.onChangeClicked && this.props.lowerInstallmentsClicked && !!this.props.monthlyPricePresentation && !!this.props.monthlyPricePresentation.main && (this.props.monthlyPricePresentation.main.price != 0 || this.props.monthlyPricePresentation.main.priceWithoutVouchers != 0);
    }

    createDefaultDescription() {
        return (
            <td colSpan={this.props.priceless && '3'} className="u-padding-l u-padding-top-l u-padding-right-l">
                <div className="u-left">
                    <span className="u-font-bold">{ this.props.title }</span><br />
                    { this.props.description &&
                        <div>{ this.props.description }</div>
                    }
                    { this.props.msisdn && this.props.processType === 'ACTIVATION' &&
                        <div>nowy numer { this.transformMsisdn(this.props.msisdn) } <a onClick={this.props.onMsisdnChangeClicked} className="u-spacing-left">Zmień</a></div>
                    }
                    { this.props.msisdn && OnlineUtils.isMnp(this.props.processType) &&
                        <div>przenoszę numer { this.transformMsisdn(this.props.mnpData.msisdn) }</div>
                    }
                    {this.props.processType !== 'SALE_OF_GOODS' &&
                        <div className="u-spacing-top">
                            <a href="#" className="u-spacing-right-l" onClick={ this.props.onChangeClicked } aria-label="zmień">Zmień</a>
                            <a href="#" className="u-spacing-right-l" onClick={ this.props.onDetailsClicked } aria-label="wyświetl szczegółowy opis">Szczegóły</a>
                            <a href="#" onClick={ preventDefaultWrapper(this.props.onRemoveClicked) } aria-label="usuń pozycję z koszyka">Usuń</a>
                        </div>
                    }
                    {this.props.processType === 'SALE_OF_GOODS' &&
                        <div className="u-spacing-top">
                            <a href="#" onClick={preventDefaultWrapper(this.props.onRemoveClicked)} aria-label="usuń pozycję z koszyka">Usuń</a>
                        </div>
                    }
                </div>
                <div className="u-right" style={{width: '50%'}}>
                    <OraStockLevelStatusComponent stockLevelStatus={this.props.stockLevelStatus} shouldStockLevelBeVisible={this.props.shouldStockLevelBeVisible}/>
                </div>
            </td>
        );
    }

    render() {
        const showLowerInstallments = this.showLowerInstallments();
        return (
            <tr>
                <MultiCartItemIconCellComponent rowSpan="5" entryType={this.props.type} image={this.props.imageUrl} icon={this.props.icon} terminalName={this.props.title}/>
                    {this.createDescription.apply(this)}
              { !this.props.priceless && !this.props.simFreeOneTimePrice &&
                <td className="l-col-2 u-padding-l u-padding-top-l u-padding-right u-text-right">
                  <span className="u-acc-hide">Opłaty jednorazowe</span>
                    <div>
                        { this.props.oneTimePrice ? this.renderPriceDesktop(this.props.oneTimePrice, this.props.stockLevelStatus) : this.renderVoidDesktop() }
                    </div>
                    {this.prepareCrossedOneTimePrice(this.props.simFreeOneTimePrice)}
                </td>
              }
              { !this.props.priceless && this.props.entryType === 'TERMINAL' && this.props.simFreeOneTimePrice !== undefined &&
                <td className="l-col-2 u-padding-l u-padding-top-l u-padding-right u-text-right">
                  <span className="u-acc-hide">Opłaty jednorazowe</span>
                    <div>
                  { this.renderPriceDesktop(this.props.simFreeOneTimePrice, this.props.stockLevelStatus) }
                    </div>
                    {this.prepareCrossedOneTimePrice(this.props.simFreeOneTimePrice)}
                </td>
              }
              { !this.props.priceless &&
                    <td className={"l-col-2 u-padding-top-l u-padding-right u-text-right" + (showLowerInstallments ? "" : " u-padding-l")}>
                        <span className="u-acc-hide">Opłaty miesięczne</span>
                        {this.props.monthlyPricePresentation.tooltip  ?
                            <ProductOfferPriceTooltipComponent key={this.props.monthlyPricePresentation.tooltip.id} {...this.props.monthlyPricePresentation.tooltip}>
                                {this.renderPrice("u-table-cell u-vertical-middle")}
                            </ProductOfferPriceTooltipComponent>
                            :
                            this.renderPrice()
                        }
                        { this.props.monthlyPricePresentation.crossedOut &&
                            <div className="g-gray5-c u-font-bold u-text-line-through u-clear-both">
                                { this.props.monthlyPricePresentation.crossedOut }
                            </div>
                        }
                        { this.props.monthlyPricePresentation.subtitle &&
                            <div className="g-gray5-c u-font-small u-clear-both">
                                { this.props.monthlyPricePresentation.subtitle }
                            </div>
                        }
                        {showLowerInstallments &&
                        <div>
                            <a className="u-spacing-left" href="#" key='lowerInstallmentsClicked_key'
                               onClick={preventDefaultWrapper(this.props.lowerInstallmentsClicked)}
                               aria-label="Obniż raty">{!!this.props.descriptions && this.props.descriptions.lowerInstallments || "Obniż raty"}
                            </a>
                        </div>
                        }
                    </td>
              }
            </tr>
        )
    }
}